@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap');

* {
  margin : none;
  outline: none;
}

body {
  background-image : url("./images/background.png");
  background-size  : 100em;
  background-repeat: no-repeat;
}

body {
  justify-content: center;
  color          : white;
  font-family    : 'Roboto', sans-serif;
}



.main-title {
  position: absolute;
  top     : 0;
}

.main-form {
  padding       : 3%;
  padding-bottom: 6%;

  margin       : 18vh 30vw 0 30vw;
  font-size    : larger;
  font-weight  : 300;
  border-radius: 25px;
  box-shadow   : 6px 6px 20px #000000;
  background   : linear-gradient(144deg, rgba(103, 8, 140, 1) 0%, rgba(30, 180, 187, 1) 100%);
  line-height  : 3em;
}


input {
  margin          : 0;
  outline         : none;
  border          : none;
  background-color: transparent;
  border-bottom   : solid;
  font-family     : 'Roboto', sans-serif;
  font-weight     : 400;
  font-size       : 1.5em;
  width           : 100%;
  color           : white;
}

.button-wrapper {
  line-height: 1;
  position   : relative;
}

.loader {
  position         : absolute;
  top              : 0;
  right            : 0;
  border           : 8px solid #f3f3f3;
  border-radius    : 50%;
  border-top       : 8px solid #ff0000;
  width            : 25px;
  height           : 25px;
  -webkit-animation: spin 2s linear infinite;
  /* Safari */
  animation        : spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.button {
  padding      : 0.5em;
  width        : 50%;
  text-align   : center;
  border-radius: 0.4em;
  font-family  : 'Roboto', sans-serif;
  font-weight  : 400;
  box-shadow   : 3px 3px 2px #333333de;
  transition   : all 0.1s ease-in;
}

.button:hover {
  cursor    : pointer;
  background: rgb(80, 79, 79);
}

.on {
  background: rgb(18, 199, 63);
}

.stop {
  background: orangered;
}

.server-url {
  word-wrap: break-word;
}

.disabled {
  visibility: hidden;
}

.enabled {
  visibility: visible;
}


@media only screen and (max-width: 1200px) {
  .button {
    width : 80%;
    margin: none;
  }

  .main-form {
    padding: 2%;
    margin : 10vh 15vw 0 15vw;
  }
}


@media only screen and (max-width: 700px) {
  .main-title {
    font-size: 12px;
  }

  input {
    font-size: 20px;
  }

  .button {
    box-shadow: 1px 1px 1px #333333de;
  }

  .main-form {
    padding       : 3%;
    padding-bottom: 5%;
    margin        : 10vh 0 0 0;
    /* box-shadow    : none; */
    font-size     : large;
    font-weight   : 300;
    border-radius : 15px;
    background    : linear-gradient(144deg, rgba(103, 8, 140, 1) 40%, rgba(30, 180, 187, 1) 100%);
    line-height   : 3em;
  }
}